<template>
  <div class="login">
		<div id="header"><van-nav-bar title=""
		left-arrow @click-left="onClickLeft"/></div>
		<div id="content"><van-form @submit="onSubmit">
		  
		  <div style="margin: 16px;"><van-field
				label-width = "4em"
		    v-model="username"
		    name="username"
		    label=""
		    placeholder=""
				left-icon="tubiao211"
		    :rules="[{ validator, message: '516' }]"
		  /></div>
			<div style="margin: 16px"><van-field
				label-width = "4em"
		    v-model="password"
		    type="password"
		    name="password"
		    label=""
		    placeholder=""
				left-icon="mima"
		    :rules="[{ validator, message: '816' }]"
		  /></div>
			<div style="margin: 16px"><van-field
				label-width = "4em"
			  v-model="repassword"
			  type="password"
			  name="repassword"
			  label=""
			  placeholder=""
				left-icon="mima"
			  :rules="[{ validator, message: '' }]"
			/></div>
		  <div style="margin: 16px;">
		    <van-button round block type="info" native-type="submit"></van-button>
		  </div>
		</van-form></div>
		<div id="footer">
		</div>
  </div>
</template>

<script>
import Vue from 'vue';
import user from '../user.js'
import VueRouter from 'vue-router'
import { Toast } from 'vant';

Vue.use(VueRouter).use(Toast);

export default {
  name: 'Login',
  props: {
    msg: String
  },
	
	data() {
	  return {
	    username: '',
	    password: '',
			repassword: '',
			pattern: /^[a-zA-Z0-9_-]{5,16}$/,
	  };
	},
	methods: {
	  onSubmit(values) {
			var register_user = JSON.parse(JSON.stringify(user));
			if (!localStorage.getItem(values.username)){
				register_user.username = values.username;
				register_user.password= values.password;
				register_user.user_logo = require('../assets/img/tx.jpg');
				localStorage.setItem(values.username, JSON.stringify(register_user));
				// localStorage.removeItem("loglevel:webpack-dev-server");
				const tost1 = Toast.loading({
					duration: 1000, //  toast
					forbidClick: true,
					message: '',
				});
				setTimeout(()=>{
					tost1.clear();
					this.$router.push('login');
					Toast.success("");
				},1200);
			}
			else{
				Toast("");
				this.username = this.password = this.repassword = null;
			}
	  },
		onClickLeft() {
			this.$router.push('login');
		},
		validator(val,rule) {
			switch(rule.message){
				case '516':
					return /^[a-zA-Z0-9_-]{5,16}$/.test(val);
				case '816':
					return /^.*(?=.{8,16})(?=.*\d)(?=.*[a-zA-Z]).*$/.test(val);
				case '':
					return this.password == this.repassword;
				default:
					return false;
			}
		}
	},
	computed: {
		// get() {
		// 	return this.$store.state.user;
		// }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login{
	background-color: #F7F8FA;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

#footer{
	text-align: center;
}
</style>
